@import url(https://fonts.googleapis.com/css?family=Public+Sans:400,500,600&display=swap);
:root{--font-body: 'Public Sans', sans-serif;--color-white: #fff;--color-black: #000;--color-offblack: #161616;--color-grey-light: #F8F9FC;--color-blue-dark: #333570;--color-yellow: #FBD792}*,::after,::before{box-sizing:border-box}html,body{margin:0;padding:0}html{font-size:62.5%}body{background-color:#F8F9FC;background-color:var(--color-grey-light);color:#161616;color:var(--color-offblack);font-size:1.8rem;font-weight:400;font-family:'Public Sans', sans-serif;font-family:var(--font-body);line-height:1.5}h1,h2,h3,h4{font-weight:500}p{margin:0 0 2.4rem}p:last-child{margin-bottom:0}a{color:#333570;color:var(--color-blue-dark);font-weight:500;-webkit-transition:all .25s ease-out;transition:all .25s ease-out}

main{background:var(--color-white);padding:3.2rem 6rem}@media (min-width: 768px){main{max-width:1000px;margin:0 auto}}

.header{background-color:var(--color-yellow);display:flex;align-items:center;padding:1rem 3.6rem}.header h1{font-size:2rem}.site-title a{color:var(--color-offblack);font-weight:600;text-decoration:none;text-transform:uppercase}.menu{margin-left:auto}.menu a{color:var(--color-offblack);margin-right:2rem}.menu a:last-child{margin-right:0}.menu a:hover{text-decoration:none}

.card{background-color:var(--color-white);margin-bottom:6rem;-webkit-transform:translate(0);transform:translate(0);border-radius:4px;border-bottom:none !important;box-shadow:0 1px 1px #e6e6e6;-webkit-transition:all .25s ease-out;transition:all .25s ease-out}.card h2{color:var(--color-blue-dark)}.card:hover{box-shadow:0 1rem 2.5rem #e6e6e6;-webkit-transform:translateY(-2px);transform:translateY(-2px)}.card img{width:100%;border-top-left-radius:4px;border-top-right-radius:4px}.card-content{padding:2rem 4rem 6rem}.card-content a{color:var(--color-offblack);background:var(--color-yellow);display:inline-block;font-size:1.6rem;font-weight:600;margin-top:1.2rem;padding:1.2rem 1.4rem;border-radius:4px;text-decoration:none}@media (min-width: 768px){.cards{display:grid;grid-gap:6rem;grid-template-columns:1fr 1fr}}

main{background:var(--color-white);padding:3.2rem 6rem}.section-header{margin-bottom:6rem;padding-bottom:2rem;border-bottom:1px solid #e1e1e1}.section-header h1{margin-bottom:0.8rem}

label{color:#333;display:block;font-size:1.6rem;font-weight:500;margin-bottom:0.8rem}input,textarea{width:100%;font-size:1.8rem;padding:1.6rem;max-width:48rem;outline:none;border-radius:4px;border:1px solid #ddd;-webkit-transition:0.25s all ease-in-out;transition:0.25s all ease-in-out}input:focus,textarea:focus{border:1px solid #bbb}textarea{min-height:20rem}.form-field{margin-bottom:3.6rem}button{color:var(--color-offblack);cursor:pointer;background:var(--color-yellow);display:inline-block;font-size:1.8rem;font-weight:600;width:100%;max-width:48rem;padding:1.6rem 1.4rem;border:none;outline:none;border-radius:4px;letter-spacing:1px;text-decoration:none}

